                 v-model="monitoredMixedObj[2].name" @keyup.enter="$refs.theSecondInput.focus()">
        </div>
        <div class="inputZone">
          <input type="number" :disabled="!abledToSelectInsurance3 || disableMark" placeholder="请如实填写保额" ref="theSecondInput"
                 v-model="monitoredMixedObj[2].insured_amount" style="width: 200px;min-width:200px;">
        </div>
        <p style="margin-left:20px;margin-bottom:7px;font-size:20px;font-weight:bold;">￥</p>
      </div>
      <div class="contentLine line4">
        <div class="checkbox">
          <label :style="disableMark?{'color':'#CCCCCC','cursor':'not-allowed'}:{}">自行购买-意外险<input type="checkbox" v-model="abledToSelectInsurance4" v-show="false"></label>
        </div>
        <div class="substitute-element"

              @click="disableMark?'':abledToSelectInsurance4 = !abledToSelectInsurance4"
              :class="disableMark?'blurStyle':''">
              <span
              class="substitute-element-inner"
              :style="abledToSelectInsurance4? {
                    backgroundColor: 'var(--color-background-button-green)',
                    border: 'none',
                  }
                : {}
            "
          ></span></div>
        <div class="inputZone">
          <input type="text" :disabled="!abledToSelectInsurance4 || disableMark" placeholder="请填写保险公司及名称" ref="theThirdInput"
                 v-model="monitoredMixedObj[3].name" @keyup.enter="$refs.theFourthInput.focus()">
        </div>
        <div class="inputZone">
          <input type="number" :disabled="!abledToSelectInsurance4 || disableMark" placeholder="请如实填写保额" ref="theFourthInput"
                 v-model="monitoredMixedObj[3].insured_amount" style="width: 200px;min-width:200px;">
        </div>
        <p style="margin-left:20px;margin-bottom:7px;font-size:20px;font-weight:bold;">￥</p>
      </div>
    </div>
  </div>
</template>

<script>
import { arrayDeepEqualDetect } from '../../../../../common/utils';
export default {
  name: 'InsuranceSelect',
  props: {
    labelTitle: {
      type: String,
      default: ''
    },
    dataToBeSelected: {
      type: Object,
      default() {
        return {};
      }
    },
  },
  data() {
    return {
      disableMark: false,
      insurance1ToBeSelect: [
        {
          type: '委托购买 - 组织者责任险',
          name: "天安营探专属产品",
          insured_amount: 100000,
          insured_amount_chinese: '10万'
        },
        {
          type: '委托购买 - 组织者责任险',
          name: "天安营探专属产品",
          insured_amount: 200000,
          insured_amount_chinese: '20万'
        },
        {
          type: '委托购买 - 组织者责任险',
          name: "天安营探专属产品",
          insured_amount: 300000,
          insured_amount_chinese: '30万'
        }
      ],
      insurance2ToBeSelect: [
        {
          type: '委托购买 - 意外险',
          name: "天安一般保障",
          insured_amount: 100000,
          insured_amount_chinese: '10万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安一般保障",
          insured_amount: 200000,
          insured_amount_chinese: '20万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安一般保障",
          insured_amount: 300000,
          insured_amount_chinese: '30万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安救援责任",
          insured_amount: 100000,
          insured_amount_chinese: '10万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安救援责任",
          insured_amount: 200000,
          insured_amount_chinese: '20万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安救援责任",
          insured_amount: 300000,
          insured_amount_chinese: '30万'
        },
        {
          type: '委托购买 - 意外险',
          name: "天安低费率",
          insured_amount: 50000,
          insured_amount_chinese: '5万'
        }
      ],
      insurance1SelectedContent: '',
      insurance2SelectedContent: '',

      abledToSelectInsurance1: false,
      abledToSelectInsurance2: false,
      abledToSelectInsurance3: false,
      abledToSelectInsurance4: false,

      originalAbledToSelectInsurance1: false,
      originalAbledToSelectInsurance2: false,
      originalAbledToSelectInsurance3: false,
      originalAbledToSelectInsurance4: false,

      originalMixedObj: null,
      monitoredMixedObj: [
        {
          type: '委托购买 - 组织者责任险',
          name: '',
          insured_amount: '',
        },
        {
          type: '委托购买 - 意外险',
          name: '',
          insured_amount: '',
        },
        {
          type: '自购 - 组织者责任险',
          name: '',
          insured_amount: '',
        },
        {
          type: '自购 - 意外险',
          name: '',
          insured_amount: '',
        },
      ],
      targetData: null,

      componentStatusCode: '100',
    }
  },
  watch: {
    abledToSelectInsurance1(newVal, oldVal) {
      if(!newVal) {
        this.insurance1SelectedContent = '';
      }

      if(this.componentStatusCode === '101') {
        if(newVal !== this.originalAbledToSelectInsurance1) {
          this.$emit('setUpdateStatusTrue');
        }
        else {
          if(oldVal === false && newVal === true && this.originalAbledToSelectInsurance1 === true) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }
      }

      this.$bus.$emit('updateDataInCookie');
    },
    insurance1SelectedContent(newVal) {
      if(newVal) {
        console.log(newVal)
        this.$set(this.monitoredMixedObj[0], 'name', newVal.name);
        this.$set(this.monitoredMixedObj[0], 'insured_amount', newVal.insured_amount);
      }
      else {
        this.$set(this.monitoredMixedObj[0], 'name','');
        this.$set(this.monitoredMixedObj[0], 'insured_amount', '');
      }
    },
    abledToSelectInsurance2(newVal, oldVal) {
      if(!newVal) {
        this.insurance2SelectedContent = '';
      }

      if(this.componentStatusCode === '101') {
        if(newVal !== this.originalAbledToSelectInsurance2) {
          this.$emit('setUpdateStatusTrue');
        }
        else {
          if(oldVal === false && newVal === true && this.originalAbledToSelectInsurance2 === true) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }
      }

      this.$bus.$emit('updateDataInCookie');
    },
    insurance2SelectedContent(newVal) {
      if(newVal) {
        console.log(newVal)
        this.$set(this.monitoredMixedObj[1], 'name', newVal.name);
        this.$set(this.monitoredMixedObj[1], 'insured_amount', newVal.insured_amount);
      }
      else {
        this.$set(this.monitoredMixedObj[1], 'name', '');
        this.$set(this.monitoredMixedObj[1], 'insured_amount', '');
      }
    },
    abledToSelectInsurance3(newVal, oldVal) {
      if(newVal) {
        setTimeout(() => {
          this.$refs.theFirstInput.focus();
        }, 500)
      }
      else {
        this.$set(this.monitoredMixedObj[2], 'name', '');
        this.$set(this.monitoredMixedObj[2], 'insured_amount', '');
      }

      if(this.componentStatusCode === '101') {
        if(newVal !== this.originalAbledToSelectInsurance3) {
          this.$emit('setUpdateStatusTrue');
        }
        else {
          if(oldVal === false && newVal === true && this.originalAbledToSelectInsurance3 === true) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }
      }

      this.$bus.$emit('updateDataInCookie');
    },
    abledToSelectInsurance4(newVal, oldVal) {
      if(newVal) {
        setTimeout(() => {
          this.$refs.theThirdInput.focus();
        }, 500)
      }
      else {
        this.$set(this.monitoredMixedObj[3], 'name', '');
        this.$set(this.monitoredMixedObj[3], 'insured_amount', '');
      }

      if(this.componentStatusCode === '101') {
        if(newVal !== this.originalAbledToSelectInsurance4) {
          this.$emit('setUpdateStatusTrue');
        }
        else {
          if(oldVal === false && newVal === true && this.originalAbledToSelectInsurance4 === true) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }
      }

      this.$bus.$emit('updateDataInCookie');
    },
    monitoredMixedObj: {
      handler(newVal) {
        if(this.componentStatusCode === '101') {
          if(this.originalMixedObj === null) {
            this.$emit('setUpdateStatusFalse');
          }
          else {
            const copy = window.JSON.parse(window.JSON.stringify(newVal));
            for(const item of copy) {
              if(item.name !== '') {
                item.name = item.name.trim();
              }
              if(item.insured_amount !== '' && typeof item.insured_amount === 'string') {
                item.insured_amount = item.insured_amount.trim();
              }
            }
            const flag = arrayDeepEqualDetect(copy, this.originalMixedObj);
            if(flag) {
              this.$emit('setUpdateStatusFalse');
            }
            else {
              this.$emit('setUpdateStatusTrue');
            }
          }
        }
        const copy = window.JSON.parse(window.JSON.stringify(newVal));
        let noEmptyFlag = false;//假设为空
        for(const item of copy) {
          if(item.name !== '' && item.insured_amnt !== '') {
            noEmptyFlag = true;
          }
        }
        if(copy[2].insured_amount !== '') {
          copy[2].insured_amount = Number(copy[2].insured_amount);
        }
        if(copy[3].insured_amount !== '') {
          copy[3].insured_amount = Number(copy[3].insured_amount);
        }
        if(noEmptyFlag) {
          this.targetData = window.JSON.parse(window.JSON.stringify(copy));
          this.$emit('setNoEmptyTrue');
          this.$bus.$emit('updateDataInCookie');
        }
        else {
          this.targetData = null;
          this.$emit('setNoEmptyFalse');
          this.$emit('setLegitimacyFalse');
          this.$bus.$emit('updateDataInCookie');
          return;
        }

        if(newVal[0].name !== '' && newVal[0].insured_amount !== '') {
          if((newVal[2].name === '' && newVal[2].insured_amount !== '') ||
             (newVal[2].name !== '' && newVal[2].insured_amount === '') ||
             (newVal[3].name === '' && newVal[3].insured_amount !== '') ||
             (newVal[3].name !== '' && newVal[3].insured_amount === '')) {
            this.$emit('setLegitimacyFalse');
          }
          else {
            this.$emit('setLegitimacyTrue');
          }
        }
        else {
          this.$emit('setLegitimacyFalse');
        }
      },
      deep: true,
    }
  },
}
</script>

