<template>
  <div class="locationSelect">
    <div class="label">
      {{labelTitle}}
    </div>
    <div class="contentZone">
      <div class="selectSetZone">
        <div class="selectZone">
          <select v-model="provinceSelectedIndex"
                  @change="provinceSelected"
                  :disabled="disableMark"
                  :title="monitoredMixedObj.provinceName">
            <option value="" disabled>省(必选)</option>
            <option v-for="(item, index) in allAdministrativeDivisionsArray"
                    :key="index"
                    :value="index">{{item.name}}</option>
          </select>
          <div style="margin-left:-20px;z-index:-1;">
            <img src="../../../../../assets/image/common/down-12.svg">
          </div>
        </div>
        <div class="selectZone">
          <select v-model="citySelectedIndex"
                  @change="citySelected"
                  :disabled="disableMark"
                  :title="monitoredMixedObj.cityName">
            <option value="" disabled>市(必选)</option>
            <option v-for="(item, index) in activeCitiesArray"
                    :key="index"
                    :value="index">{{item.name}}</option>
          </select>
          <div style="margin-left:-20px;z-index:-1;">
            <img src="../../../../../assets/image/common/down-12.svg">
          </div>
        </div>
        <div class="selectZone">
          <select v-model="countySelectedIndex"
                  @change="countySelected"
                  :disabled="disableMark"
                  :title="monitoredMixedObj.countyName">
            <option value="" disabled>区县(必选)</option>
            <option v-for="(item, index) in activeCountiesArray"
                    :key="index"
                    :value="index">{{item}}</option>
          </select>
          <div style="margin-left:-20px;z-index:-1;">
            <img src="../../../../../assets/image/common/down-12.svg">
          </div>
        </div>
      </div>
      <div class="inputZone">
        <label for="concreteAddress" style="min-width:150px;">详细地址(必填)：</label>
        <input type="text"
               id="concreteAddress"
               v-model="monitoredMixedObj.detailAddress"
               :disabled="disableMark">
      </div>
    </div>
  </div>
</template>

<script>
import districtData from '../../../../../assets/json/city.json'
export default {
  name: 'e-LocationSelect',
  async created() {
    const arr = [];
    for(const key1 in districtData) {
      const obj1 = {
        name: key1,
        sub: [],
      }
      for(const key2 in districtData[key1]) {
        const obj2 = {
          name: key2,
          sub: districtData[key1][key2],
        }
        obj1.sub.push(obj2);
      }
      arr.push(obj1);
    }
    this.allAdministrativeDivisionsArray = window.JSON.parse(window.JSON.stringify(arr));
  },
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      disableMark: false,

      allAdministrativeDivisionsArray: [],

      activeCitiesArray: [],
      activeCountiesArray: [],

      provinceSelectedIndex: '',
      citySelectedIndex: '',
      countySelectedIndex: '',

      monitoredMixedObj: {
        provinceName: '',
        cityName: '',
        countyName: '',
        detailAddress: '',
      },

      originalMixedObj: {
        provinceName: '',
        cityName: '',
        countyName: '',
        detailAddress: '',
      },

      componentStatusCode: '100',
      targetData: null,
    }
  },
  methods: {
    provinceSelected() {
      const pIndex = Number(this.provinceSelectedIndex);
      this.$set(this.monitoredMixedObj, 'provinceName', this.allAdministrativeDivisionsArray[pIndex].name);

      this.activeCitiesArray = this.allAdministrativeDivisionsArray[pIndex].sub;

      // reset城市、区县的selectedValue和essenceValue
      this.citySelectedIndex = '';
      this.$set(this.monitoredMixedObj, 'cityName', '');
      this.countySelectedIndex = '';
      this.$set(this.monitoredMixedObj, 'countyName', '');
    },
    citySelected() {
      const pIndex = Number(this.provinceSelectedIndex);
      const cIndex = Number(this.citySelectedIndex);
      this.$set(this.monitoredMixedObj, 'cityName', this.allAdministrativeDivisionsArray[pIndex].sub[cIndex].name);

      this.activeCountiesArray = this.allAdministrativeDivisionsArray[pIndex].sub[cIndex].sub;

      this.countySelectedIndex = '';
      this.$set(this.monitoredMixedObj, 'countyName', '');
    },
    countySelected() {
      const pIndex = Number(this.provinceSelectedIndex);
      const cIndex = Number(this.citySelectedIndex);
      const countyIndex = Number(this.countySelectedIndex);

      this.$set(this.monitoredMixedObj, 'countyName', this.allAdministrativeDivisionsArray[pIndex].sub[cIndex].sub[countyIndex]);
    },
  },
  watch: {
    monitoredMixedObj: {
      handler(newVal) {
        if(newVal.provinceName !== '' && newVal.cityName !== '' &&
        newVal.countyName !== '' && newVal.detailAddress.trim() !== '') {
          this.$emit('setLegitimacyTrue');
        }
        else {
          this.$emit('setLegitimacyFalse');
        }

        if(this.componentStatusCode === '100') {
          if(newVal.provinceName !== '' && newVal.cityName !== '' &&
          newVal.countyName !== '' && newVal.detailAddress.trim() !== '') {
            this.$emit('setNoEmptyTrue');
          }
          else {
            this.$emit('setNoEmptyFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          if(newVal.provinceName !== this.originalMixedObj.provinceName ||
          newVal.cityName !== this.originalMixedObj.cityName ||
          newVal.countyName !== this.originalMixedObj.countyName ||
          newVal.detailAddress.trim() !== this.originalMixedObj.detailAddress) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }

        const copy = window.JSON.parse(window.JSON.stringify(newVal));
        copy.detailAddress = copy.detailAddress.trim();
        this.targetData = copy;

        this.$bus.$emit('updateDataInCookie');
      },
      deep: true,
    }
  },
}
</script>

