<template>
  <div class="teachingSyllabus">
    <div class="label">教学提纲</div>
    <div class="operationZone">
      <div class="mainContent">
        <div class="addAndDeleteArea">
          <div v-for="(item, index) in teachingSyllabus" :key="index" class="adAreaItem">
            <div class="firstLine">
              <input type="text" placeholder="请填写学习章节" v-model="item.title">
              <button @click="removeItem(index)">删除</button>
            </div>
            <div class="secondLine">
              <textarea placeholder="1.本章节目录1 换行   2.本章节目录2 换行   3.本章节目录3 换行"
                        v-model="item.details"></textarea>
            </div>
          </div>
        </div>
        <button class="addBtn" @click="addItem">+添加章节</button>
      </div>
    </div>
  </div>
</template>

<script>
import { debounce, arrayDeepEqualDetect, processStringAsArray } from '../../../../../common/utils'
export default {
  name: 'e-TeachingSyllabus',
  created() {
    const emitTrue = () => {
      this.$emit('setLegitimacyTrue');
    }
    this.debounceEmitTrue = debounce(emitTrue, 500);

    const emitFalse = () => {
      this.$emit('setLegitimacyFalse');
    }
    this.debounceEmitFalse = debounce(emitFalse, 500);

    const emitUpdateTrue = () => {
      this.$emit('setUpdateStatusTrue');
    }
    this.debounceEmitUpdateTrue = debounce(emitUpdateTrue, 500);

    const emitUpdateFalse = () => {
      this.$emit('setUpdateStatusFalse');
    }
    this.debounceEmitUpdateFalse = debounce(emitUpdateFalse,500);
  },
  data() {
    return {
      // 教学提纲数据存放
      teachingSyllabus: [
        {
          title: '',
          details: '',
        },
      ],

      debounceEmitTrue: null,
      debounceEmitFalse: null,

      originalTeachingSyllabus: [],
      componentStatusCode: '100',
      targetData: null,
      debounceEmitUpdateTrue: null,
      debounceEmitUpdateFalse: null,
    }
  },
  methods: {
    // 点击【+添加章节】按钮触发该事件
    addItem() {
      const newObj = {
        title: '',
        details: '',
      }
      this.teachingSyllabus.push(newObj);

      setTimeout(() => {
        const scrollTop = document.body.scrollTop || document.documentElement.scrollTop;
        const elementHeight = document.querySelector('.teachingSyllabus').clientHeight;
        const elementOffsetTop = document.querySelector('.teachingSyllabus').offsetTop;
        const windowHeight = document.body.clientHeight;

        const n1 = scrollTop + windowHeight;
        //console.log('点击生成新的div后屏幕内可以展示到的文档的最大高度:'+n1);
        const n2 = elementOffsetTop + elementHeight + 90;
        //console.log('展示该项完整图文到底需要到达的高度:'+n2);
        // 如果屏幕可视区域内可以展示到的文档的最大高度 小于 展示teachingSyllabus组件在屏幕最底部之上需要的文档高度
        if(n1 < n2) {
          const distance = n2 - n1;
          const step = distance / 30;
          // console.log(distance);
          // console.log(step);
          let count = 0;
          const scrollInterval = setInterval(function() {
            if(count < distance) {
              document.documentElement.scrollTop += step;
              document.body.scrollTop += step;
              count += step;
            }
            else {
              clearInterval(scrollInterval);
            }
          }, 1);
        }
      }, 100);
    },
    // 删除input+textarea时触发该方法的执行
    removeItem(index) {
      this.teachingSyllabus.splice(index, 1);
    },
  },
  watch: {
    teachingSyllabus: {
      handler(newVal) {
        this.$bus.$emit('updateDataInCookie');

        let flag = true;
        for(let i = 0; i < newVal.length; i++) {
          if(newVal[i].title === '' || newVal[i].details === '') {
            flag = false;
          }
        }
        if(flag) {
          this.debounceEmitTrue();
        }
        else {
          this.debounceEmitFalse();
        }

        if(this.componentStatusCode === '100') {
          let noEmptyFlag = true;
          for(const item of newVal) {
            if(item.title === '' || item.details === '') {
              noEmptyFlag = false;
            }
          }
          if(noEmptyFlag) {
            this.$emit('setNoEmptyTrue');
          }
          else {
            this.$emit('setNoEmptyFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          // 先对v-model数据进行处理
          const tempArr = [];
          for(const item of newVal) {
            const obj = {
              title: item.title.trim(),
              details: processStringAsArray(item.details).join('\n'),
            }
            tempArr.push(obj);
          }
          const f = arrayDeepEqualDetect(tempArr, this.originalTeachingSyllabus);
          if(f) {
            this.$emit('setUpdateStatusFalse');
          }
          else {
            this.debounceEmitUpdateTrue();
          }
        }

        this.targetData = window.JSON.parse(window.JSON.stringify(newVal));
        for(const index in this.targetData) {
          this.targetData[index].title = this.targetData[index].title.trim();
          this.targetData[index].details = processStringAsArray(this.targetData[index].details);
        }
      },
      deep: true,
    }
  }
}
</script>

