  <!-- 可选多个 -->
  <template>

  <div class="multiSelect">
    <div class="label">
      {{labelTitle}}
    </div>
    <div class="inputZone">
      <div class="mainContainer">
        <div v-for="(item, index) in checkboxObjectList" :key="item.id" class="item">
          <label>{{item.label}}<input type="checkbox"
                                      :value="item.value"
                                      v-model="selected"
                                      v-show="false"></label>
          <span class="substitute-element"
                @click="pseudoSpanClick(index)"
                :style="selected.indexOf(item.value)!==-1?{'backgroundColor':'var(--color-background-button-green)',border:'none'}:{}"></span>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'e-CheckboxMultiSelect',
  props: {
    // 标签text
    labelTitle: {
      type: String,
      default: '',
    },
    // 待选择的复选框组对应的数据列表
    checkboxObjectList: {
      type: Array,
      default() {
        return [];
      },
    },
    activityStatus: {
      type: String,
      default: '0000',
    },
  },
  data() {
    return {
      // 被选择的复选框对应的value组成的列表
      selected: [],

      originalSelected: [],
      componentStatusCode: '100',
      targetData: null,
    }
  },
  methods: {
    // 复选框标签右边的span被点击时触发该方法的执行
    pseudoSpanClick(index) {
      // 检查被点击的复选框是被选中状态还是未被选中状态
      const i = this.selected.indexOf(this.checkboxObjectList[index].value);
      // 未被选中
      if(i === -1) {
        this.selected.push(this.checkboxObjectList[index].value);
      }
      // 被选中
      else {
        this.selected.splice(i, 1);
      }
    }
  },
  watch: {
    selected: {
      handler(newVal) {
        if(newVal.length !== 0) {
          this.$emit('setLegitimacyTrue');
        }
        else {
          this.$emit('setLegitimacyFalse');
        }

        if(this.componentStatusCode === '100') {
          if(newVal.length !== 0) {
            this.$emit('setNoEmptyTrue');
          }
          else {
            this.$emit('setNoEmptyFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          if(newVal.length !== this.originalSelected.length) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            let flag = true;
            for(const item of newVal) {
              const one = this.originalSelected.find(i => i === item);
              if(!one) {
                flag = false;
              }
            }
            if(flag) {
              this.$emit('setUpdateStatusFalse');
            }
            else {
              this.$emit('setUpdateStatusTrue');
            }
          }
        }

        if(newVal.length !== 0) {
          this.targetData = newVal;
        }
        else {
          this.targetData = null;
        }
      },
      deep: true,
    }
  }
}
</script>

<style scoped>
  .multiSelect {
    margin-top: 30px;
    width: 100%;
    display: flex;
    align-items: center;
  }
  .multiSelect .label {
    width: 160px;
    font-weight: bold;
    min-width: 160px;
  }
  .multiSelect .inputZone {
    flex: 1;
  }
  .multiSelect .inputZone .mainContainer {
    width: 74%;
    display: flex;
  }
  .multiSelect .inputZone .mainContainer .item {
    width: 30%;
  }
  .multiSelect .inputZone .mainContainer .item label {
    padding-right: 6px;
  }
  .multiSelect .inputZone .mainContainer .item span.substitute-element {
    display: inline-block;
    width: 23px;
    height: 23px;
    vertical-align: middle;
    margin-top: -4px;
    cursor: pointer;

    border: 1px solid #000;
    
    transition: all .5s ease;
    -webkit-transition: all .5s ease;
  }
</style>
