  <!-- 这个组件在表单中暂时也不用了 -->
<template>

  <div class="refundInstruction">
    <div class="label">
      {{labelTitle}}
    </div>
    <div class="contentZone">
      <p>活动开始前<input type="number" placeholder="请填写数字" v-model="monitoredMixedObj.num1">天，可退全额</p>
      <p>活动开始前<input type="number" placeholder="请填写数字" v-model="monitoredMixedObj.num2">天至<input type="number" placeholder="请填写数字" v-model="monitoredMixedObj.num3">天，可退半额</p>
      <p>活动开始前<input type="number" placeholder="请填写数字" v-model="monitoredMixedObj.num4">天至<input type="number" placeholder="请填写数字" v-model="monitoredMixedObj.num5">天，不可退款</p>
    </div>
  </div>
</template>

<script>
import { objectDeepEqualDetect } from '../../../../../common/utils'
export default {
  name: 'e-RefundInstruction',
  props: {
    labelTitle: {
      type: String,
      default: '',
    },
  },
  data() {
    return {
      monitoredMixedObj: {
        num1: '',
        num2: '',
        num3: '',
        num4: '',
        num5: '',
      },
      originalMixedObj: {
        num1: '',
        num2: '',
        num3: '',
        num4: '',
        num5: '',
      },
      componentStatusCode: '100',
      targetData: null,
    };
  },
  watch: {
    monitoredMixedObj: {
      handler(newVal) {
        if(newVal.num1 !== '' &&
           newVal.num2 !== '' &&
           newVal.num3 !== '' &&
           newVal.num4 !== '' &&
           newVal.num5 !== '' &&
           Number(newVal.num1) > Number(newVal.num2) &&
           Number(newVal.num2) > Number(newVal.num3) &&
           Number(newVal.num3) > Number(newVal.num4) &&
           Number(newVal.num4) > Number(newVal.num5)) {
             this.$emit('setLegitimacyTrue');
           }
        else {
          this.$emit('setLegitimacyFalse');
        }

        if(this.componentStatusCode === '100') {
          if(newVal.num1 !== '' ||
            newVal.num2 !== '' ||
            newVal.num3 !== '' ||
            newVal.num4 !== '' ||
            newVal.num5 !== '') {
              this.$emit('setNoEmptyTrue');
            }
          else {
            this.$emit('setNoEmptyFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          if(newVal.num1 !== this.originalMixedObj.num1 ||
          newVal.num2 !== this.originalMixedObj.num2 ||
          newVal.num3 !== this.originalMixedObj.num3 ||
          newVal.num4 !== this.originalMixedObj.num4 ||
          newVal.num5 !== this.originalMixedObj.num5) {
            this.$emit('setUpdateStatusTrue');
          }
          else {
            this.$emit('setUpdateStatusFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          const f = objectDeepEqualDetect(newVal, this.originalMixedObj);
          if(f) {
            this.$emit('setUpdateStatusFalse');
          }
          else {
            this.$emit('setUpdateStatusTrue');
          }
        }

        this.targetData = [
          {
            name: '全额退款',
            ratio: '100',
            start: 10000,
            end: Number(newVal.num1),
          },
          {
            name: '半额退款',
            ratio: '50',
            start: Number(newVal.num2),
            end: Number(newVal.num3),
          },
          {
            name: '不退款',
            ratio: '0',
            start: Number(newVal.num4),
            end: Number(newVal.num5),
          },
        ];
      },
      deep: true,
    }
  }
}
</script>

