<template>
  <div class="videoUpload">
    <div class="label">
      {{ labelTitle }}
    </div>
    <div class="content">
      <div class="description">
        请上传一段时长不超过2分钟，大小10M以内的短视频，内容为行前说明或活动风采展示
      </div>
      <div class="line">
        <div class="uploadImg" @click="btnClick">
          <input
            type="file"
            @change="onFileChange"
            id="videoUpload"
            v-show="false"
          />
          <img
            src="../../../../../assets/image/providerTools/uploadVideo.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            点击上传
          </div>
          <!-- <button @click="btnClick">点击上传</button> -->
        </div>
        <transition name="myTransition">
          <div
            class="url"
            v-if="videoUrl !== ''"
          >
            <div class="urlContent">
              <video
                :src="videoUrl"
                controls="controls"
              ></video>
              <div class="delete" @click="resetVideoUrl" title="删除"></div>
            </div>
          </div>
        </transition>
      </div>
    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import { $uploadStreamMediaFile } from "../../../../../network/common/common";
export default {
  name: "e-VideoUpload",
  props: {
    labelTitle: {
      type: String,
      default: "",
    },
  },
  data() {
    return {
      videoUrl: "",
      originalVideoUrl: "",
      componentStatusCode: "100",
      targetData: "",

      isLoading: false,
    };
  },
  methods: {
    async onFileChange(e) {
      const file_input = document.getElementById("videoUpload");
      const files = e.target.files || e.dataTransfer.files;

      if (!files.length) return;

      if (!/video\/\w+/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求", 240);
        file_input.value = "";
        return;
      }

      if (files[0].size / 1024 / 1024 > 100.0) {
        this.$toast.error("视频大小超过10M");
        file_input.value = "";
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);

      try {
        const response = await $uploadStreamMediaFile(formData);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.$toast.success("上传成功");
            this.videoUrl = details.image_url;
            this.$emit("updateCompsOffsetTop");
          } else {
            this.$toast.error("上传失败");
          }
        } else {
          this.$toast.error("上传失败");
        }

        file_input.value = "";
        this.isLoading = false;
      } catch (err) {
        this.$toast.error("上传失败");
        file_input.value = "";
        this.isLoading = false;
      }
    },
    btnClick() {
      document.getElementById("videoUpload").click();
    },
    resetVideoUrl() {
      this.videoUrl = "";
      document.getElementById("videoUpload").value = "";
      this.$emit("updateCompsOffsetTop");
    },
  },
  watch: {
    videoUrl(newValue) {
      if (newValue !== "") {
        this.$emit("setLegitimacyTrue");
      } else {
        this.$emit("setLegitimacyFalse");
      }

      if (this.componentStatusCode === "100") {
        if (newValue !== "") {
          this.$emit("setNoEmptyTrue");
        } else {
          this.$emit("setNoEmptyFalse");
        }
      }

      if (this.componentStatusCode === "101") {
        if (newValue !== this.originalVideoUrl) {
          this.$emit("setUpdateStatusTrue");
        } else {
          this.$emit("setUpdateStatusFalse");
        }
      }

      this.targetData = newValue;

      this.$bus.$emit("updateDataInCookie");
    },
  },
};
</script>

