            accept="image/jpg, image/jpeg"
          />
          <img
            src="../../../../../assets/image/providerTools/uploadImage.png"
            alt=""
            style="width: 50px"
          />
          <div style="margin-top: 5px; font-size: 18px; color: gray">
            点击上传
          </div>
          <!-- <p class="clickText" @click="triggerFileInput">点击上传</p> -->
        </div>
      </div>

      <!-- 横版 -->
      <div v-if="!isvertical" class="right">
        <!-- <div v-for="(pic, index) in pictureList" :key="pic.id" class="right-item">
          <img :src="pic.url" @click="showPicPreview(pictureList, index)" title="查看大图"> -->
