              >当前图片展示名称：{{
                pic.name === "" ? "#empty" : pic.name
              }}，可在下方输入框输入字符修改图片展示名称</template
            >
            <!-- <p class="pictureName">请填写图片的展示名称</p> -->
          </a-tooltip>
          <input
            class="pictureNameInput"
            type="text"
            v-model="pic.name"
            placeholder="请输入图片展示名称"
          />
          <a-tooltip placement="left">
            <!-- <template #title>删除本张图片</template> -->
            <div
              class="cancel"
              title="删除"
              @click="deletePicture(index)"
            ></div>
          </a-tooltip>
        </div>
      </div>
      <!-- 竖版 -->
      <div v-if="isvertical" class="right">

        <!-- <div v-for="(pic, index) in pictureList" :key="pic.id" class="right-item">
          <img :src="pic.url" @click="showPicPreview(pictureList, index)" title="查看大图"> -->
        <div
          v-for="(pic, index) in pictureList"
          :key="pic.id"
          class="right-item vertical-item"
        >
          <img
            :src="pic.url"
            @click="showPicPreview(pictureList, index)"
            style="width: 120px; height: 186px"
          />
          <a-tooltip placement="topLeft">
            <template #title
              >当前图片展示名称：{{
                pic.name === "" ? "#empty" : pic.name
              }}，可在下方输入框输入字符修改图片展示名称</template
            >
            <!-- <p class="pictureName">请填写图片的展示名称</p> -->
          </a-tooltip>
          <input
            class="pictureNameInput"
            type="text"
            v-model="pic.name"
