    </div>

    <div class="fixedFullLoading" v-show="isLoading">
      <a-space>
        <a-spin size="large"></a-spin>
      </a-space>
    </div>
  </div>
</template>

<script>
import $ from "jquery";
import uuid from "uuid";
import Sortable from "sortablejs";
import { $uploadStreamMediaFile } from "../../../../../network/common/common";
import {
  deleteFileNameSuffix,
  readImageFileToDetectRatio,
  uniqueArr,
} from "../../../../../common/utils";
export default {
  name: "e-PicturePresentZone",
  props: {
    requireText: {
      type: String,
      default: "",
    },
    requireText2: {
      type: String,
      default: "",
    },
    requireText3: {
      type: String,
      default: "",
    },
    fileInputId: {
      type: String,
      default: "",
    },
    idealRatio: {
      type: Number,
      default: 1,
    },
    isvertical:{
      type:Boolean,
      default:false
    }
  },
  data() {
    return {
      pictureList: [],
      originalPictureList: [],
      componentStatusCode: "100",
      targetData: null,

      sortList: Object,
      isLoading: false,
    };
  },
  mounted() {
    console.log('isvertical',this.isvertical)
    console.log('ratio ratio')
    console.log('ideall',this.idealRatio)
    const $ul = this.$el.querySelector(".right");
    this.sortList = Sortable.create($ul, {
      handle: ".right-item",
      draggable: ".right-item",
      animation: 250,
      onUpdate: (event) => {
        const newIndex = event.newIndex;
        const oldIndex = event.oldIndex;
        const $li = $ul.children[newIndex];
        const $oldLi = $ul.children[oldIndex];
        // 先删除移动的节点
        $ul.removeChild($li);
        // 再插入移动的节点到原有节点，还原了移动的操作
        if (newIndex > oldIndex) {
          $ul.insertBefore($li, $oldLi);
        } else {
          $ul.insertBefore($li, $oldLi.nextSibling);
        }
        const item = this.pictureList[event.oldIndex];
        this.pictureList.splice(event.oldIndex, 1);
        this.pictureList.splice(event.newIndex, 0, item);
      },
    });
  },
  methods: {
    triggerFileInput() {
      if (this.pictureList.length === 10) {
        this.$toast.error("图片数量已达上限");
        return;
      }
      document.getElementById(`${this.fileInputId}`).click();
    },
    async onFileChange(e) {
      const file_input = document.querySelector(`#${this.fileInputId}`);
      let files = e.target.files || e.dataTransfer.files;
      //console.log(files[0]);
      //console.log(e.target.value);
      // 若e.target.files为空，则不继续往下进行
      if (!files.length) return;
      const originalpic = files[0]
      const ret=await this.$print.getPrintedFile(files[0])
      console.log(ret)
      files=[ret]
      // 判断图片格式是否为jpg格式【已经往input中添加accept属性】
      if (!/image\/jp(e)g/.test(files[0].type)) {
        this.$toast.error("文件格式不符要求");
        file_input.value = "";
        return;
      }

      // 判断图片大小是否小于等于2MB
      const size = originalpic.size / 1024; //单位：kb
      if (size > 2048) {
        this.$toast.error("图片大小超2MB");
        file_input.value = "";
        return;
      }

      try {
        const res = await readImageFileToDetectRatio(files[0], this.idealRatio);
        console.log('readImageFileToDetectRatio')
        if (res.code !== "1111") {
          this.$toast.error("图片长宽比例不符要求", 240);
          file_input.value = "";
          this.isLoading = false;
          return;
        }
      } catch (err) {
        this.$toast.error("图片读取失败", 240);
        file_input.value = "";
        this.isLoading = false;
        return;
      }

      this.isLoading = true;

      const formData = new FormData();
      formData.append("image", files[0]);
      try {
        const response = await $uploadStreamMediaFile(formData);
        const { Code, msg } = response;
        if (Code === 200 && msg === "success") {
          const { details } = response;
          if (details.image_url) {
            this.pictureList.push({
              id: uuid.v4(),
              name: deleteFileNameSuffix(files[0].name),
              url: details.image_url,
            });
            this.$emit("updateCompsOffsetTop");
            this.$toast.success("图片上传成功", 240);
          } else {
            this.$toast.error("上传失败", 240);
          }
        } else {
          this.$toast.error("上传失败", 240);
        }

        file_input.value = "";
        setTimeout(() => {
          this.isLoading = false;
        }, 600);
      } catch (err) {
        this.$toast.error("上传失败", 240);
        file_input.value = "";
        this.isLoading = false;
      }
    },
    deletePicture(index) {
      this.pictureList.splice(index, 1);
      this.$emit("updateCompsOffsetTop");
    },
    showPicPreview(pictureList, index) {
      this.$picPreview.list = pictureList;
      this.$picPreview.activeIndex = index;
      this.$picPreview.ifShowPicPreview = true;
    },
  },
  watch: {
    pictureList: {
      handler(newVal) {
        this.$bus.$emit("updateDataInCookie");

        if (newVal.length !== 0) {
          this.targetData = newVal;
        } else {
          this.targetData = null;
        }

        // 先判断更新情况
        if (this.componentStatusCode === "101") {
          if (newVal.length !== this.originalPictureList.length) {
            this.$emit("setUpdateStatusTrue");
          }
          // 长度相同
          else {
            let flag = true; // 预设newVal与originalPictureList完全相同
            for (const index in newVal) {
              if (
                newVal[index].url !== this.originalPictureList[index].url ||
                newVal[index].name !== this.originalPictureList[index].name
              ) {
                flag = false;
              }
            }
            if (flag) {
              this.$emit("setUpdateStatusFalse");
            } else {
              this.$emit("setUpdateStatusTrue");
            }
          }
        }
        if (newVal.length === 0) {
          this.$emit("setNoEmptyFalse");
          return;
        }else {
          this.$emit("setNoEmptyTrue");
        }
        // 判断当前值的合法性
        // 先从数量上进行判断
        if (newVal.length < 2) {
          this.$emit("setLegitimacyFalse");
          this.$emit("setErrorHint", "图片数量不得少于2张");
          return;
        }
        if (newVal.length > 9) {
          this.$emit("setLegitimacyFalse");
          this.$emit("setErrorHint", "图片数量不得多于9张");
          return;
        }

        // 数量满足条件的基础上，对是否有图片名称为空进行检查
        for (const item of newVal) {
          if (item.name === "") {
            this.$emit("setLegitimacyFalse");
            this.$emit("setErrorHint", "图片名字不能为空");
            return;
          }
        }

        // 在前面检查条件通过的情况下对图片名称是否存在重复进行检查
        const tempArray = [];
        for (const item of newVal) {
          tempArray.push(item.name);
        }
        const newArray = uniqueArr(tempArray);
        if (tempArray.length !== newArray.length) {
          this.$emit("setLegitimacyFalse");
          this.$emit("setErrorHint", "图片名字不能重复");
          return;
        }

        this.$emit("setLegitimacyTrue");
        this.$emit("setNoEmptyTrue");
        this.$emit("setErrorHint", "");
      },
      deep: true,
    },
  },
};
</script>

