<template>
  <div class="textInputPlusTextarea">
    <div class="title">
      <label :for="textInputId">{{labelTitle}}</label>
    </div>
    <div class="inputAndTextareaZone">
      <div class="inputZone">
        <input type="text"
                :id="textInputId"
                :placeholder="textInputPlaceholder"
                v-model="monitoredMixedObj.shortMessage"
                @input="checkInputWhenInput"
                @keyup.enter="changeFocus"
                :style="errorColor?{color: 'red'}:{}">
        <div class="warn" v-show="errorColor">
          <div class="warnText" :class="textInputId">不能为空</div>
        </div>
      </div>
      <textarea :id="textareaId"
                :placeholder="textareaPlaceholder"
                v-model="monitoredMixedObj.longMessage"
                :style="{height:textareaHeight}"></textarea>
    </div>
  </div>
</template>

<script>
import { debounce, processStringAsArray } from '../../../../../common/utils';
export default {
  name: 'e-TextInputPlusTextarea',
  props: {
    labelTitle: {
      type: String,
      default: '',
    },
    textInputId: {
      type: String,
      default: '',
    },
    textInputPlaceholder: {
      type: String,
      default: '',
    },

    textareaId: {
      type: String,
      default: '',
    },
    textareaPlaceholder: {
      type: String,
      default: '',
    },
    textareaHeight: {
      type: String,
      default: '160px',
    },
  },
  created() {
    const emitTrue = () => {
      this.$emit('setLegitimacyTrue');
    }
    this.debounceEmitTrue = debounce(emitTrue, 500);

    const emitFalse = () => {
      this.$emit('setLegitimacyFalse');
    }
    this.debounceEmitFalse = debounce(emitFalse, 500);

    function emitUpdateTrue() {
      this.$emit('setUpdateStatusTrue');
    }
    this.debounceEmitUpdateTrue = debounce(emitUpdateTrue, 500);

    function emitUpdateFalse() {
      this.$emit('setUpdateStatusFalse');
    }
    this.debounceEmitUpdateFalse = debounce(emitUpdateFalse, 500);
  },
  data() {
    return {
      monitoredMixedObj: {
        shortMessage: '',
        longMessage: '',
      },
      debounceEmitTrue: null,
      debounceEmitFalse: null,

      errorColor: false,

      originalMixedObj: {
        shortMessage: '',
        longMessage: '',
      },
      componentStatusCode: '100',
      debounceEmitUpdateTrue: null,
      debounceEmitUpdateFalse: null,

      targetData: {
        title: '',
        content: null,
      },
    };
  },
  methods: {
    changeFocus() {
      document.querySelector(`#${this.textareaId}`).focus();
    },
    checkInputWhenInput(e) {
      const result = /\S/.test(e.target.value);
      this.errorColor = result? false: true;
    },
  },
  watch: {
    monitoredMixedObj: {
      handler(newVal) {
        if(newVal.shortMessage !== '' && newVal.longMessage !== '') {
          this.debounceEmitTrue();
        }
        else {
          this.debounceEmitFalse();
        }

        if(this.componentStatusCode === '100') {
          if(newVal.shortMessage !== '' && newVal.longMessage !== '') {
            this.$emit('setNoEmptyTrue');
          }
          else {
            this.$emit('setNoEmptyFalse');
          }
        }

        if(this.componentStatusCode === '101') {
          if(newVal.shortMessage.trim() !== this.originalMixedObj.shortMessage ||
          processStringAsArray(newVal.longMessage).join('\n') !== this.originalMixedObj.longMessage) {
            this.debounceEmitUpdateTrue();
          }
          else {
            this.debounceEmitUpdateFalse();
          }
        }

        this.$set(this.targetData, 'title', newVal.shortMessage);
        if(newVal.longMessage !== '') {
          const arr = processStringAsArray(newVal.longMessage);
          this.$set(this.targetData, 'content', arr);
        }
        else {
          this.$set(this.targetData, 'content', null);
        }

        this.$bus.$emit('updateDataInCookie');
      },
      deep: true,
    },
  },
}
</script>

