          val.referenceName !== "clickToSelectFeature" &&
          val.referenceName !== "clickToSelectInvoice"
        ) {
          this.$refs[val.referenceName].$data.componentStatusCode = "101";
        }
      }
      this.categorySelectCompStatus = "101";
      this.featureSelectCompStatus = "101";
      this.invoiceSelectCompStatus = "101";
    });
    const vm = this;
    // 监听文档scroll
    window.addEventListener("scroll", this.handleScroll, true);
    // 监听窗口宽度和高度变化，主要是为了调整【活动类别选择弹框】组件和【活动特色选择弹框】组件的top和left值
    window.addEventListener("resize", function () {
      const dX = vm.$refs[vm.compList[6].referenceName].$el.offsetLeft + 160;
      const dY = vm.$refs[vm.compList[6].referenceName].$el.offsetTop;
      const sX = vm.$refs[vm.compList[13].referenceName].$el.offsetLeft + 160;
      const sY = vm.$refs[vm.compList[13].referenceName].$el.offsetTop;
      const mX = vm.$refs[vm.compList[14].referenceName].$el.offsetLeft + 160;
      const mY = vm.$refs[vm.compList[14].referenceName].$el.offsetTop;
      setTimeout(() => {
        vm.invoiceSelectDialogLeft = dX;
        vm.invoiceSelectDialogTop = dY;
        vm.categorySelectDialogLeft = sX;
        vm.categorySelectDialogTop = sY;
        vm.featureSelectDialogLeft = mX;
        vm.featureSelectDialogTop = mY;

        for (let i = 0; i < vm.compList.length; i++) {
          vm.$set(
            vm.compList[i],
            "offsetTop",
            vm.$refs[vm.compList[i].referenceName].$el.offsetTop
          );
        }
      }, 100);
    });

    window.addEventListener("beforeunload", this.beforeUnloadHandler, false);

    this.currentUrl = window.location.href; // 记录当前页面url
  },
  unmounted() {
    window.removeEventListener("beforeunload", this.beforeUnloadHandler, false);
  },
  beforeRouteLeave(to, from, next) {
    if (this.updateMark) {
      if (confirm("您有修改未提交，确认离开？")) {
        next();
      } else {
        next(false);
      }
    } else {
      next();
    }
